.sasw_drawer {
	--sa-padding: 1em;
	max-width: 100%;
	max-height: 100%;

	.sasw_drawer_wrap {
		display: flex;
		flex-direction: column;
		height: 100%;
	}

	.sasw_drawer_actions,
	.sasw_drawer_btn {
		display: flex;
		align-items: center;
		gap: 10px;
	}

	.sasw_drawer_head_inner,
	.sasw_drawer_footer_inner {
		min-height: 1em;
		display: flex;
		align-items: center;
	}

	.sasw_drawer_head,
	.sasw_drawer_footer {
		padding: calc(var(--sa-padding) / 2) var(--sa-padding);
		align-items: center;
	}

	.sasw_drawer_head {
		border-bottom: 1px solid var(--sa-border-color);
		flex: 0;
	}

	.sasw_drawer_footer {
		flex-shrink: 0;
		border-top: 1px solid var(--sa-border-color);
	}

	.sasw_drawer_title {
		font-size: 1em;
		font-weight: 600;
	}

	.sasw_drawer_actions {
		margin-left: auto;
	}

	.sasw_drawer_body {
		flex: 1;
		overflow: auto;
		padding: var(--sa-padding);
	}
}


// -------------

.drawer-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  opacity: 0;
	z-index: 999000;
  background-color: rgba(0, 0, 0, 0.5);
  transition: opacity 225ms cubic-bezier(0.4, 0, 0.2, 1);
	&.o {
		opacity: 1;
		z-index: 999000;
		visibility: visible;
	}
	&.h {
		opacity: 0;
		z-index: -1;
		visibility: hidden;
		transition: 225ms cubic-bezier(0.4, 0, 0.2, 1) 0ms;
	}
	
}


.drawer {
  top: 0;
  flex: 1 0 auto;
  height: 100%;
  display: flex;
  outline: 0;
	z-index: 999100;
  position: fixed;
  overflow-y: auto;
  flex-direction: column;
  background-color: #ffffff;
  width: 300px;
  flex-shrink: 0;

  transition: box-shadow 300ms cubic-bezier(0.4, 0, 0.2, 1) 0ms,
  transform 225ms cubic-bezier(0, 0, 0.2, 1) 0ms;
	&.h {
		visibility: hidden;
		// width: 240px;
		flex-shrink: 0;
		transition: 325ms cubic-bezier(0, 0, 0.2, 1) 0ms;
	}

	&.animate {
		visibility: visible;
		transform: none;
	}

	&.left {
		left: 0;
		right: auto;
		&.h {
			transform: translateX(-100%);
		}
	}
	
	&.right {
		right: 0;
		left: auto;
		&.h {
			transform: translateX(100%);
		}
	}
	&.bottom {
		right: 0;
		left: 0;
		top: auto;
		bottom: 0;
		&.h {
			transform: translateY(100%);
		}
	}
	
}







